import uniCollapse from '@/components/uni-collapse/uni-collapse.vue';
import uniCollapseItem from '@/components/uni-collapse-item/uni-collapse-item.vue';
import uniNumberBox from '@/components/uni-number-box/uni-number-box.vue';
import uniList from "@/components/uni-list/uni-list.vue";
import uniListItem from "@/components/uni-list-item/uni-list-item.vue";
import {
	mapState
} from 'vuex';
export default {
	components: {
		uniCollapse,
		uniCollapseItem,
		uniNumberBox,
		uniList,
		uniListItem
	},
	data() {
		return {
			specClass: 'none',
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 1,
			flist: [],
			slist: [],
			tlist: [],
			scrollHeight: '400px',
			leftArray: [],
			mainArray: [],
			leftIndex: 0,
			addressData: {
				address: '',
				city: '',
				id: '',
				name: '',
				phone: '',
				province: '',
				zone: '',
				id: '',
				defaultFlag: 0,
			},
			total: 0,
			goodTemp: {},
			orderTemp: {},
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
		}
	},
	computed: {
		/* 计算左侧滚动位置定位 */
		leftIntoView() {
			return `left-${this.leftIndex > 5 ? (this.leftIndex-5):0}`;
		},
		...mapState(['hasLogin', 'userInfo'])
	},
	mounted() {
		/* 等待DOM挂载完成 */
		this.$nextTick(() => {
			/* 在非H5平台，nextTick回调后有概率获取到错误的元素高度，则添加200ms的延迟来减少BUG的产生 */
			setTimeout(() => {
				/* 等待滚动区域初始化完成 */
				this.initScrollView().then(() => {
					/* 获取列表数据，你的代码从此处开始 */
				})
			}, 200);
		})
	},
	onShow() {
		if (!this.hasLogin) {
			this.addressData = {
				address: '',
				city: '',
				id: '',
				name: '',
				phone: '',
				province: '',
				zone: '',
				id: '',
				defaultFlag: 0,
			};
		}
		this.getListData();
	},
	onLoad() {
		if (this.hasLogin) {
			this.loadAddressInfo();
		} else {
			this.addressData = {
				address: '',
				city: '',
				id: '',
				name: '',
				phone: '',
				province: '',
				zone: '',
				id: '',
				defaultFlag: 0,
			}
		}
	},
	methods: {
		navTo(url) {
			if (!this.hasLogin) {
				uni.showModal({
					title: '温馨提示',
					content: '此时此刻需要您登录喔~',
					confirmText: "去登录",
					cancelText: "再逛会",
					success: res2 => {
						if (res2.confirm) {
							uni.navigateTo({
								url: "/pages/public/login"
							});
						}
					}
				});
			} else {
				uni.navigateTo({
					url
				})
			}

		},
		stopPrevent() {},
		toggleSpec() {
			if (this.specClass === 'show') {
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			} else if (this.specClass === 'none') {
				this.specClass = 'show';
			}
		},
		calcTotal() {
			let order = this.orderTemp;
			let totalPrice = 0;
			for (var key in order) {
				totalPrice += order[key].totalPirceAmount;
			}
			this.total = totalPrice;
		},
		//数量
		numberChange(changeNumber, index) {
			changeNumber = parseInt(changeNumber)
			let goodDetail = this.goodTemp[index];
			if (changeNumber == 0) {
				delete this.orderTemp[goodDetail.goodDetailId];
			} else {
				goodDetail["num"] = parseInt(changeNumber);
				let deliveryFeeAmount = 0;
				if (goodDetail.pinkAge > 1 && changeNumber % goodDetail.pinkAge > 0) {
					deliveryFeeAmount = parseFloat(goodDetail.deliveryFee);
					goodDetail["deliveryFeeAmount"] = " , 运费：" + goodDetail.deliveryFee + "元";
				}
				goodDetail["totalPirce"] = "￥" + (parseFloat(goodDetail.price) * changeNumber + deliveryFeeAmount);
				goodDetail["totalPirceAmount"] = parseFloat(goodDetail.price) * changeNumber + deliveryFeeAmount;
				goodDetail["totalDeliveryFee"] = deliveryFeeAmount;
				let deliveryDesc = goodDetail.deliveryFeeAmount;
				goodDetail["desc"] = "单价：" + goodDetail.price + "元 , 数量：" + changeNumber;
				if(deliveryDesc != undefined){
					goodDetail["desc"] += deliveryDesc
				}
				this.orderTemp[goodDetail.goodDetailId] = goodDetail;
			}
			this.calcTotal();
		},
		//一级分类点击
		tabtap(item) {
			if (!this.sizeCalcState) {
				this.calcSize();
			}

			this.currentId = item.id;
			let index = this.slist.findIndex(sitem => sitem.pid === item.id);
			this.tabScrollTop = this.slist[index].top;
		},
		//右侧栏滚动
		asideScroll(e) {
			if (!this.sizeCalcState) {
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item => item.top <= scrollTop).reverse();
			if (tabs.length > 0) {
				this.currentId = tabs[0].pid;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize() {
			let h = 0;
			this.slist.forEach(item => {
				let view = uni.createSelectorQuery().select("#main-" + item.id);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		/* 初始化滚动区域 */
		initScrollView() {
			return new Promise((resolve, reject) => {
				let view = uni.createSelectorQuery().select('#scroll-panel');
				view.boundingClientRect(res => {
					this.scrollHeight = `${res.height}px`;
					this.$nextTick(() => {
						resolve();
					})
				}).exec();
			});
		},
		/* 获取列表数据 */
		getListData() {
			// Promise 为 ES6 新增的API ，有疑问的请自行学习该方法的使用。
			new Promise((resolve, reject) => {
				/* 因无真实数据，当前方法模拟数据。正式项目中将此处替换为 数据请求即可 */
				uni.showLoading();
				var that = this;
				this.$http.get(this.$httpApi.goods.category, {}).
				then(function(response) {
					let [left, main] = [
						[],
						[]
					];

					for (let i = 0; i < response.length; i++) {
						left.push(response[i].name);
						let goodDtos = response[i].goodDtos;
						let list = [];
						for (let j = 0; j < goodDtos.length; j++) {
							let goodDto = goodDtos[j];
							list.push(goodDto);

							let goodDetails = goodDto.goodDetails;
							for (var e = 0; e < goodDetails.length; e++) {
								let goodDetailDto = goodDetails[e];
								goodDetailDto["num"] = 0;
								that.goodTemp[goodDetailDto.goodDetailId] = goodDetailDto;
							}

						}
						main.push({
							title: response[i].name,
							list
						})
					}

					// 将请求接口返回的数据传递给 Promise 对象的 then 函数。
					resolve({
						left,
						main
					});

				}).catch(function(error) {
					//这里只会在接口是失败状态返回，不需要去处理错误提示
				});

			}).then((res) => {
				console.log('-----------请求接口返回数据示例-------------');
				console.log(res);

				uni.hideLoading();
				this.leftArray = res.left;
				this.mainArray = res.main;
			});
		},
		/* 左侧导航点击 */
		leftTap(e) {
			let index = e.currentTarget.dataset.index;
			this.leftIndex = Number(index);
		},
		/* 轮播图切换 */
		swiperChange(e) {
			let index = e.detail.current;
			this.leftIndex = Number(index);
		},
		loadAddressInfo() {
			var that = this;
			this.$http.get(this.$httpApi.address.defaultAddr, {}).
			then(function(response) {
				//这里只会在接口是成功状态返回
				that.addressData.address = response.address;
				that.addressData.city = response.city;
				that.addressData.id = response.id;
				that.addressData.name = response.name;
				that.addressData.phone = response.phone;
				that.addressData.province = response.province;
				that.addressData.zone = response.zone;
			}).catch(function(error) {
				//这里只会在接口是失败状态返回，不需要去处理错误提示
			});
		},
		showOrder() {
			if (!this.hasLogin) {
				uni.showModal({
					title: '温馨提示',
					content: '此时此刻需要您登录喔~',
					confirmText: "去登录",
					cancelText: "再逛会",
					success: res2 => {
						if (res2.confirm) {
							uni.navigateTo({
								url: "/pages/public/login"
							});
						}
					}
				});
				return
			}

			if (!this.total || this.total == 0) {
				this.$api.msg("请选择要购买的商品!!!");
				return;
			}
			this.toggleSpec();
		},
		makeOrder() {

			if (!this.total || this.total == 0) {
				this.$api.msg("请选择要购买的商品!!!");
				return;
			}
			if (this.addressData.id == null || this.addressData.id == '') {
				this.$api.msg("请选择收货地址")
				return
			}
			var that = this;
			uni.showModal({
				title: '提示',
				content: '确认提交订单？',
				success: function(res) {
					if (res.confirm) {
						let orderInfo = {};
						let addressId = that.addressData.id;
						let goodDetails = [];
						let nums = [];
						let orderType = 1;
						for (var key in that.orderTemp) {
							goodDetails.push(key);
							nums.push(that.orderTemp[key].num);
						};

						orderInfo["orderType"] = orderType;
						orderInfo["goodDetailIds"] = goodDetails;
						orderInfo["nums"] = nums;
						orderInfo["addressId"] = addressId;

						that.$http.post(that.$httpApi.order.makeOrder, orderInfo).
						then(function(response) {
							that.orderTemp = {};
							that.total = 0;
							that.$api.msg("下单成功");
							that.toggleSpec();
							setTimeout(function() {
								that.getListData();
							}, 500)
						}).catch(function(error) {
							//这里只会在接口是失败状态返回，不需要去处理错误提示
						});
					}
				}
			});
		}
	}
}
